
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
Group,
Cell,
CellFormPreview,
CellBox,
Badge,
Grid,
GridItem,
Panel,
XHeader,
ViewBox,
Flexbox,
FlexboxItem
},
data() {
return {
name: ''
};
},
mounted() {
if(!localStorage.user_info) {
this.name = "未设置"
} else {
var user_name = JSON.parse(localStorage.user_info).user_name;
this.name = user_name;
}
console.log("转换之后", JSON.parse(localStorage.user_info))
},
methods: {

toOrder() {
console.log("去我的订单页面");
this.$router.push({
path: "/newOrder/newOrder"
});
},

toOne() {
this.$router.push({
path: "/newOrder/newOrder",
name: "/newOrder/newOrder",
params: {
order_number: '1'
}
});
},

toTwo() {
this.$router.push({
path: "/newOrder/newOrder",
name: "/newOrder/newOrder",
params: {
order_number: '2'
}
});
},

toThree() {
this.$router.push({
path: "/newOrder/newOrder",
name: "/newOrder/newOrder",
params: {
order_number: '3'
}
});
},

toFour() {
this.$router.push({
path: "/newOrder/newOrder",
name: "/newOrder/newOrder",
params: {
order_number: '4'
}
});
},

toDiscount() {
console.log("去优惠页面");
},

toUserSet() {
console.log("去user页面");
this.$router.push({
path: "/userSet/userSet",



});
},

toSet() {
this.$router.push({
path: "/set/set"
});
},

toCollection() {
console.log("去收藏页面");
this.$router.push({
path: "/collect/collect"
});
},

toFeedback() {
console.log("去反馈页面");
this.$router.push({
path: "/feedback/feedback"
});
},

toTel() {
this.$router.push({
path: "/tel/tel"
});
},

toScore() {
console.log("去评分页面");
},

toMessage() {
this.$router.push({
path: "/message/message"
});
},

toNewOrder() {
this.$router.push({
path: "/newOrder/newOrder"
});
}
},
};



